@tailwind base;
@tailwind components;
@tailwind utilities;

.hero{
    background-image:radial-gradient(rgba(0,0,0,.5),rgba(0,0,0,.5)), url(./Assets/image102.jpg);
    background-size: 100% 100%;
}
.container-fluid{
    width: 80%;
    margin: 0 auto;
}
.Webinar{
    background-image:radial-gradient(rgba(0, 0,0, 0.5),rgba(0, 0, 0, 0.5)), url(./Assets/image158.webp);
    background-size: 100% 100%;
}
.card{
    height:50vh;
    background-size:100% 100%;
}
.card1{
    height:45vh;
    background-size:100% 100%;
}
.group{
    visibility: hidden;
}
.card:hover .group{
    visibility: visible;
}
.card1:hover .group{
    visibility: visible;
}